    <table class="table table-bordered">
      <thead>
        <tr>
          <th>ID</th>
          <th>Reporter</th>
          <th>Ticket No</th>
          <th>Type</th>
          <th>PG Related</th>
          <th>Notes</th>
          <th>Assigned Time</th>
          <th>Response Time</th>
          <th>Fulfillment Time</th>
          <th>Response Time SLA</th>
          <th>Fulfillment Time SLA</th>
        </tr>
        <tr>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
          <td><input /></td>
