<script>
import axios from 'axios';

export default {
  data() {
    return {
      ticket_list: {},
    };
  },
  methods: {
    getTicket: function() {
      axios
        .get(
          'http://localhost:8080/ticket/baseAPI/getlistoftickets?from=2017-02-25%2000:00:01&to=2017-02-29%2000:00:01',
        )
        .then(response => {
          console.log(response);
          this.ticket_list = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .then(function() {
          // always executed
        });
    },
  },
  beforeMount() {
    this.getTicket();
  },
};
</script>

